.app-layout {
  display: flex;
  min-height: 100vh;
  font-family: 'Poppins', sans-serif;
  /* Ensure sidebar space is reserved immediately */
  position: relative;
}

/* Ensure English text uses Poppins */
.app-layout.ltr,
html[dir="ltr"] .app-layout {
  font-family: 'Poppins', sans-serif;
}

/* Ensure Arabic text uses IBM Plex Sans Arabic */
.app-layout.rtl,
html[dir="rtl"] .app-layout {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
}

.app-layout.rtl {
  direction: rtl;
  flex-direction: row-reverse;
}

.content-area {
  flex: 1;
  margin-left: calc(var(--sidebar-w));
  transition: margin-left 0.3s ease, margin-right 0.3s ease;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* Ensure content area is properly positioned */
  position: relative;
  z-index: 1;
  /* Prevent content overlap during initial load */
  min-width: 0;
  overflow-x: hidden;
}

.app-layout.rtl .content-area {
  margin-left: 0;
  margin-right: var(--sidebar-w);
}

/* When sidebar is collapsed, content should take full width */
.app-layout .content-area.sidebar-collapsed {
  margin-left: var(--sidebar-w-collapsed);
  margin-right: 0;
}

.app-layout.rtl .content-area.sidebar-collapsed {
  margin-left: 0;
  margin-right: var(--sidebar-w-collapsed);
}

.app-header {
  background: transparent;
  padding: 1rem 2rem;
  border-radius: 15px;
  margin: 1rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  flex-direction: row;
}

.header-content :dir[ltr] {
  flex-direction: row !important;
  justify-content: space-between !important;
}
.user-profile {
  display: flex;
  align-items: center;
  gap: 1rem;
  order: 1;
  transition: all 0.2s ease;
  border-radius: 8px;
  padding: 0.5rem;
}

.user-profile:hover {
  background: rgba(212, 170, 58, 0.1);
  transform: translateY(-1px);
}

.user-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid #e5e7eb;
  flex-shrink: 0;
}

.profile-pic {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.user-name {
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  font-weight: 700;
  color: #5F646D;
  white-space: nowrap;
}

/* Mobile Sidebar Toggle Button */
.mobile-sidebar-toggle {
  background: #BFAB86;
  border: none;
  color: #FFFFFF;
  padding: 0.75rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: none;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(212, 170, 58, 0.3);
}

.mobile-sidebar-toggle:hover {
  background: #B8941F;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(212, 170, 58, 0.4);
}

.mobile-sidebar-toggle:active {
  transform: translateY(0);
  box-shadow: 0 2px 8px rgba(212, 170, 58, 0.3);
}

.mobile-sidebar-toggle svg {
  transition: transform 0.3s ease;
}

/* Mobile Sidebar Overlay */
.mobile-sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  z-index: 998;
  opacity: 0;
  animation: fadeIn 0.3s ease forwards;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  order: 2;
}

/* RTL Support - Opposite behavior: Arabic on left, English on right */
.header-actions.rtl {
  justify-content: flex-start;
  margin-right: auto;
}

.header-actions:not(.rtl) {
  justify-content: flex-end;
  margin-left: auto;
}

.header-notification-dropdown {
  position: relative;
}

.language-toggle {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(243, 244, 246, 0.8);
  padding: 0.25rem;
  border-radius: 999px;
  backdrop-filter: blur(10px);
}

.lang-btn {
  background: transparent;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 999px;
  font-family: 'Poppins', sans-serif;
  font-size: 0.875rem;
  font-weight: 500;
  color: #5F646D;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  min-height: 36px;
  min-width: 48px;
  justify-content: center;
}

.lang-btn.active {
  background: #BFAB86;
  color: #FFFFFF;
  box-shadow: 0 2px 8px rgba(212, 170, 58, 0.3);
}

.lang-btn:hover:not(.active) {
  background: #E6D7A2;
  color: #5F646D;
}

.lang-btn svg {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.lang-btn.active svg {
  transform: scale(1.1);
}

/* RTL Support */
.app-header.rtl .header-content {
  justify-content: flex-start;
}

.app-header.rtl .header-actions {
  order: 1;
  justify-content: flex-start;
  margin-right: auto;
}

.app-header:not(.rtl) .header-content {
  justify-content: flex-end;
}

.app-header:not(.rtl) .header-actions {
  order: 2;
  justify-content: flex-end;
  margin-left: auto;
}

.app-header.rtl .notification-badge {
  right: auto;
  left: -5px;
}

.page-title {
  color: var(--brand-green);
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0;
}

.header-btn {
  background: none;
  border: none;
  padding: 0.5rem;
  border-radius: 0.375rem;
  color: var(--text-secondary);
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.header-btn:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.main-content {
  flex: 1;
  background-color: white;
  padding: 2rem;
}

.main-content.sidebar-collapsed {
  margin-left: var(--sidebar-w-collapsed);
}

.app-layout.rtl .main-content.sidebar-collapsed {
  margin-left: 0;
  margin-right: var(--sidebar-w-collapsed);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  /* Show mobile toggle button */
  .mobile-sidebar-toggle {
    display: flex;
  }
  
  /* On mobile, content takes full width - override all sidebar margins */
  .content-area {
    margin-left: 0 !important;
    margin-right: 0 !important;
    transition: margin-left 0.3s ease, margin-right 0.3s ease;
  }
  
  .app-layout.rtl .content-area {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  
  .main-content {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  
  .app-layout.rtl .main-content {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  
  /* Ensure header also takes full width */
  .app-header {
    padding: 1rem;
    margin: 0.5rem 1rem !important;
  }
  
  .header-content {
    gap: 1rem;
  }
  
  .header-actions {
    gap: 1rem;
  }
  
  /* Ensure header actions maintain correct positioning on mobile */
  .header-actions.rtl {
    justify-content: flex-start;
    margin-right: auto;
  }
  
  .header-actions:not(.rtl) {
    justify-content: flex-end;
    margin-left: auto;
  }
  
  .user-name {
    font-size: 0.875rem;
  }
  
  .language-toggle {
    padding: 0.125rem;
  }
  
  .lang-btn {
    padding: 0.375rem 0.75rem;
    font-size: 0.8rem;
    min-width: 40px;
  }
  
  .main-content {
    padding: 1rem;
  }
  
  /* Ensure content flows properly when sidebar is expanded */
  .app-layout .content-area {
    transition: margin-left 0.3s ease, margin-right 0.3s ease;
  }
  
  /* When sidebar is expanded on mobile, don't push content */
  .app-layout .content-area {
    margin-left: var(--sidebar-w-collapsed);
    margin-right: 0;
  }
  
  .app-layout.rtl .content-area {
    margin-left: 0;
    margin-right: var(--sidebar-w-collapsed);
  }
}

/* Logout Confirmation Modal */
.logout-confirm-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99999;
  padding: 1rem;
}

.logout-confirm-modal {
  background: white;
  border-radius: 16px;
  padding: 2rem;
  max-width: 400px;
  width: 100%;
  position: relative;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.logout-confirm-icon {
  text-align: center;
  margin-bottom: 1.5rem;
  color: #ef4444;
}

.logout-confirm-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #111827;
  text-align: center;
  margin-bottom: 1rem;
  line-height: 1.4;
}

.logout-confirm-message {
  color: #6b7280;
  text-align: center;
  margin-bottom: 2rem;
  line-height: 1.6;
  font-size: 1rem;
}

.logout-confirm-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.btn {
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
  min-width: 100px;
}

.btn-cancel {
  background: white;
  color: #374151;
  border: 2px solid #e5e7eb;
}

.btn-cancel:hover {
  background: #f9fafb;
  border-color: #d1d5db;
}

.btn-logout {
  background: #ef4444;
  color: white;
}

.btn-logout:hover {
  background: #dc2626;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.3);
}

/* Responsive adjustments for logout modal */
@media (max-width: 480px) {
  .logout-confirm-modal {
    padding: 1.5rem;
    margin: 1rem;
  }
  .main-content {
    padding: 0.2rem;
  }
  .logout-confirm-actions {
    flex-direction: column;
  }

  .btn {
    width: 100%;
  }
}



